import * as acessoWebFrameSupport from "./frame-support.js";
import * as acessoWebFrameTypes from "./frame-types.js";

let svgMask = null;
let borderColor = null;
let borderWidth = 5;
const backgroundOpacity = "0.7";

let defs = null;
let style = null;
let groupMain = null;
let groupMask = null;
let pathBackground = null;
let pathLine = null;
let pathFocus = null;
let react1 = null;
let react2 = null;
let react3 = null;
let svgText = null;
let svgText2 = null;
let bottomSilhouetteDocument = null;
let FocusSilhouette = null;

function resetVariables() {
    svgMask = null;
    defs = null;
    style = null;
    groupMain = null;
    groupMask = null;
    pathBackground = null;
    pathLine = null;
    pathFocus = null;
    react1 = null;
    react2 = null;
    react3 = null;
    svgText = null;
    svgText2 = null;
    bottomSilhouetteDocument = null;
    FocusSilhouette = null;
}

function showToDisplayMask() {
  if(svgMask){
    svgMask.style.display = "unset";
  }
}

function hideToDisplayMask() {
  if(svgMask){
    svgMask.style.display = "none";
  }
}

function setBorderColor(color) {
    borderColor = color;
}

function setBorderWidth(value) {
    borderWidth = value;
}

function createSelfieMask(boxCamera, maskWidth, maskHeight, cameraVideoWidth, cameraVideoHeight) {
  let exists = boxCamera.querySelector("#svgMask") !== null;
  let mBoxXCenter = cameraVideoWidth / 2;
  let mBoxYCenter = cameraVideoHeight / 2;

  let halfMWidth = maskWidth / 2;
  let halfHeight = maskHeight / 2;
  let half14Height = maskHeight / 4;
  let fractionX = 0.15;
  let fractionWidthX = halfMWidth * fractionX;

  let point1X = mBoxXCenter + halfMWidth;
  let point1Y = mBoxYCenter + half14Height;

  let point2X = mBoxXCenter + fractionWidthX;
  let point2Y = mBoxYCenter + halfHeight;

  let point3H = mBoxXCenter - fractionWidthX * 2;

  let point4X = mBoxXCenter - halfMWidth;
  let point4Y = mBoxYCenter + half14Height;

  let point5V = mBoxYCenter - half14Height;

  let point6X = mBoxXCenter - fractionWidthX;
  let point6Y = mBoxYCenter - halfHeight;

  let point7h = fractionWidthX * 2;

  let point8X = mBoxXCenter + halfMWidth;
  let point8Y = mBoxYCenter - half14Height;

  let arcXY = halfMWidth - fractionWidthX;

  let mov0 = "M0,0";
  let v0 = "V" + cameraVideoHeight;
  let h0 = "H" + cameraVideoWidth;
  let v1 = "V0";
  let z0 = "Z";
  let mov1 = `M${point1X},${point1Y}`;
  let arc1 = `A${arcXY},${arcXY},0,0,1,${point2X},${point2Y}`;
  let h1 = `H${point3H}`;
  let arc2 = `A${arcXY},${arcXY},0,0,1,${point4X},${point4Y}`;
  let v2 = `V${point5V}`;
  let arc3 = `A${arcXY},${arcXY},0,0,1,${point6X},${point6Y}`;
  let h2 = `h${point7h}`;
  let arc4 = `A${arcXY},${arcXY},0,0,1,${point8X},${point8Y}`;
  let z = "Z";
  let d = `${mov0}${v0}${h0}${v1}${z0}${mov1}${arc1}${h1}${arc2}${v2}${arc3}${h2}${arc4}${z}`;
  let xmlns = "http://www.w3.org/2000/svg";

  if (!svgMask) {
    svgMask = document.createElementNS(xmlns, "svg");
  }

  svgMask.setAttributeNS(null, "viewBox", "0 0 " + cameraVideoWidth + " " + cameraVideoHeight);
  svgMask.setAttributeNS(null, "width", cameraVideoWidth);
  svgMask.style.display = "block";
  svgMask.setAttributeNS(null, "id", `svgMask`);

  if (!defs) {
    defs = document.createElementNS(xmlns, "defs");
  }

  if (!style) {
    style = document.createElementNS(xmlns, "style");
  }
  
  style.textContent = `.cls-background{opacity:${backgroundOpacity};}.cls-focus{fill:none;stroke:${borderColor};stroke-miterlimit:10;stroke-width:${borderWidth}px;}`;

  if (!groupMain) {
    groupMain = document.createElementNS(xmlns, "g");
  }

  groupMain.setAttributeNS(null, "id", `main`);
  groupMain.setAttributeNS(null, "data-name", `main`);

  if (!groupMask) {
    groupMask = document.createElementNS(xmlns, "g");
  }

  groupMask.setAttributeNS(null, "id", `mask`);

  if (!pathBackground) {
    pathBackground = document.createElementNS(xmlns, "path");
  }

  pathBackground.setAttributeNS(null, "id", `background`);
  pathBackground.setAttributeNS(null, "class", `cls-background`);
  pathBackground.setAttributeNS(null, "d", d);

  if (!pathFocus) {
    pathFocus = document.createElementNS(xmlns, "rect");
  }

  pathFocus.setAttributeNS(null, "id", `focus-silhouette`);
  pathFocus.setAttributeNS(null, "class", `cls-focus`);
  pathFocus.setAttributeNS(null, "x", point4X);
  pathFocus.setAttributeNS(null, "y", point6Y);
  pathFocus.setAttributeNS(null, "width", maskWidth);
  pathFocus.setAttributeNS(null, "height", maskHeight);
  pathFocus.setAttributeNS(null, "rx", arcXY);
  pathFocus.setAttributeNS(null, "style", `stroke: ${borderColor};`);

  if (!exists) {
    groupMask.appendChild(pathBackground);
    groupMask.appendChild(pathFocus);
    groupMain.appendChild(groupMask);
    defs.appendChild(style);
    svgMask.appendChild(defs);
    svgMask.appendChild(groupMain);
    boxCamera.appendChild(svgMask);
  }
}

function createDocumentMask(boxCamera, mWidth, mHeight, mBoxWidth, mBoxHeight) {

  let exists = boxCamera.querySelector("#svgMask") !== null;
  let mBoxXCenter = mBoxWidth / 2;
  let mBoxYCenter = mBoxHeight / 2 - Math.round((9 / 100) * mBoxHeight);

  let halfMWidth = mWidth / 2;
  let halfHeight = mHeight / 2;
  let fractionX = 0.15;
  let fractionWidthX = halfMWidth * fractionX;

  /*   5  6   7  8

       4  3   2  1 */

  let point1X = mBoxXCenter + halfMWidth;
  let point1Y = mBoxYCenter + halfHeight;

  let point2X = mBoxXCenter + fractionWidthX;
  let point2Y = mBoxYCenter + halfHeight;

  let point3H = mBoxXCenter - fractionWidthX * 2;

  let point4X = mBoxXCenter - halfMWidth;
  let point4Y = mBoxYCenter + halfHeight;

  let point5V = mBoxYCenter - halfHeight;

  let point6X = mBoxXCenter - fractionWidthX;
  let point6Y = mBoxYCenter - halfHeight;

  let point7h = fractionWidthX * 2;

  let point8X = mBoxXCenter + halfMWidth;
  let point8Y = mBoxYCenter - halfHeight;

  if (acessoWebFrameSupport.isMobile()) {
    point6Y = point6Y / 2;
    point8Y = point8Y / 2;
    point2Y = point2Y - point6Y;
    point4Y = point4Y - point6Y;
    point1Y = point1Y - point6Y;
    point5V = point5V / 2;
  }

  let arcXY = 0;
  let mov0 = "M0,0";
  let v0 = "V" + mBoxHeight;
  let h0 = "H" + mBoxWidth;
  let v1 = "V0";
  let z0 = "Z";
  let mov1 = `M${point1X},${point1Y}`;
  let arc1 = `A${arcXY},${arcXY},0,0,1,${point2X},${point2Y}`;
  let h1 = `H${point3H}`;
  let arc2 = `A${arcXY},${arcXY},0,0,1,${point4X},${point4Y}`;
  let v2 = `V${point5V}`;
  let arc3 = `A${arcXY},${arcXY},0,0,1,${point6X},${point6Y}`;
  let h2 = `h${point7h}`;
  let arc4 = `A${arcXY},${arcXY},0,0,1,${point8X},${point8Y}`;
  let z = "Z";
  let d = `${mov0}${v0}${h0}${v1}${z0}${mov1}${arc1}${h1}${arc2}${v2}${arc3}${h2}${arc4}${z}`;
  let xmlns = "http://www.w3.org/2000/svg";

  bottomSilhouetteDocument = point6Y + mHeight;

  if (!svgMask) {
    svgMask = document.createElementNS(xmlns, "svg");
  }

  svgMask.setAttributeNS(null, "viewBox", "0 0 " + mBoxWidth + " " + mBoxHeight);
  svgMask.setAttributeNS(null, "width", mBoxWidth);
  svgMask.setAttributeNS(null, "height", mBoxHeight);
  svgMask.style.display = "block";
  svgMask.setAttributeNS(null, "id", `svgMask`);

  if (!defs) {
    defs = document.createElementNS(xmlns, "defs");
  }

  if (!style) {
    style = document.createElementNS(xmlns, "style");
  }
  style.textContent = `.cls-background{opacity:${backgroundOpacity};}.cls-focus{fill:none;stroke:${borderColor};stroke-miterlimit:10;stroke-width:${borderWidth}px;}`;

  if (!groupMain) {
    groupMain = document.createElementNS(xmlns, "g");
  }

  groupMain.setAttributeNS(null, "id", `main`);
  groupMain.setAttributeNS(null, "data-name", `main`);

  if (!groupMask) {
    groupMask = document.createElementNS(xmlns, "g");
  }

  groupMask.setAttributeNS(null, "id", `mask`);

  if (!pathBackground) {
    pathBackground = document.createElementNS(xmlns, "path");
  }

  pathBackground.setAttributeNS(null, "id", `background`);
  pathBackground.setAttributeNS(null, "class", `cls-background`);
  pathBackground.setAttributeNS(null, "d", d);

  if (!pathFocus) {
    pathFocus = document.createElementNS(xmlns, "rect");
  }

  pathFocus.setAttributeNS(null, "id", `focus-silhouette`);
  pathFocus.setAttributeNS(null, "class", `cls-focus`);
  pathFocus.setAttributeNS(null, "x", point4X);
  pathFocus.setAttributeNS(null, "y", point6Y);
  pathFocus.setAttributeNS(null, "width", mWidth);
  pathFocus.setAttributeNS(null, "height", mHeight);
  pathFocus.setAttributeNS(null, "rx", arcXY);

  if (acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CNH) {
    if (!pathLine) {
      pathLine = document.createElementNS(xmlns, "path");
    }
    pathLine.setAttributeNS(null, "id", `line`);
    pathLine.setAttributeNS(
      null,
      "d",
      `M ${point4X} ${acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter
      } l ${point1X - point4X} 0`
    );
    pathLine.setAttributeNS(null, "fill", "none");
    pathLine.setAttributeNS(null, "stroke", borderColor);
    pathLine.setAttributeNS(null, "stroke-width", "3");

    if (!react1) {
      react1 = document.createElementNS(xmlns, "rect");
    }
    react1.setAttributeNS(null, "id", `rect-top-cnh`);
    react1.setAttributeNS(null, "class", `cls-focus`);
    react1.setAttributeNS(null, "x", point4X + mWidth * 0.19);
    react1.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) -
      (halfHeight * 0.13 + halfHeight * 0.57)
    );
    react1.setAttributeNS(null, "width", mWidth * 0.3);
    react1.setAttributeNS(null, "height", halfHeight * 0.57);
    react1.setAttributeNS(null, "stroke-width", 3);
    react1.setAttributeNS(null, "fill", "none");
    react1.setAttributeNS(null, "stroke", borderColor);

    if (!react2) {
      react2 = document.createElementNS(xmlns, "rect");
    }
    react2.setAttributeNS(null, "id", `rect-bottom-cnh`);
    react2.setAttributeNS(null, "class", `cls-focus`);
    react2.setAttributeNS(null, "x", point4X + mWidth * 0.16);
    react2.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) + halfHeight * 0.05
    );
    react2.setAttributeNS(null, "width", mWidth * 0.77);
    react2.setAttributeNS(null, "height", halfHeight * 0.4);
    react2.setAttributeNS(null, "stroke-width", 3);
    react2.setAttributeNS(null, "fill", "none");
    react2.setAttributeNS(null, "stroke", borderColor);

    if (!svgText) {
      svgText = document.createElementNS(xmlns, "text");
    }

    svgText.setAttributeNS(null, "id", `text1`);
    svgText.setAttributeNS(null, "class", `cls-text`);
    svgText.setAttributeNS(null, "x", point4X + mWidth * 0.19);
    svgText.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) -
      (halfHeight * 0.13 + halfHeight * 0.57) -
      10
    );
    svgText.setAttributeNS(null, "fill", borderColor);
    svgText.setAttributeNS(null, "stroke", borderColor);
    svgText.innerHTML = "";
    let textNode = document.createTextNode("FOTO");
    svgText.appendChild(textNode);
  } else if (acessoWebFrameTypes.isRGFront()) {
    if (!react1) {
      react1 = document.createElementNS(xmlns, "rect");
    }
    react1.setAttributeNS(null, "id", `rect1`);
    react1.setAttributeNS(null, "class", `cls-focus`);
    react1.setAttributeNS(null, "x", point4X + (mWidth - mWidth * 0.45) / 2);
    react1.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) - mHeight * 0.4
    );
    react1.setAttributeNS(null, "width", mWidth * 0.45);
    react1.setAttributeNS(null, "height", mHeight * 0.4);
    react1.setAttributeNS(null, "stroke-width", 3);
    react1.setAttributeNS(null, "fill", "none");
    react1.setAttributeNS(null, "stroke", borderColor);

    if (!react2) {
      react2 = document.createElementNS(xmlns, "rect");
    }
    react2.setAttributeNS(null, "id", `rect2`);
    react2.setAttributeNS(null, "class", `cls-focus`);
    react2.setAttributeNS(null, "x", point4X + (mWidth - mWidth * 0.45) / 2);
    react2.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) + mHeight * 0.05
    );
    react2.setAttributeNS(null, "width", mWidth * 0.45);
    react2.setAttributeNS(null, "height", mHeight * 0.38);
    react2.setAttributeNS(null, "stroke-width", 3);
    react2.setAttributeNS(null, "fill", "none");
    react2.setAttributeNS(null, "stroke", borderColor);

    if (!svgText) {
      svgText = document.createElementNS(xmlns, "text");
    }
    svgText.setAttributeNS(null, "id", `text1`);
    svgText.setAttributeNS(null, "class", `cls-text`);
    svgText.setAttributeNS(null, "x", mBoxXCenter - 25);
    svgText.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) - mHeight * 0.42
    );
    svgText.setAttributeNS(null, "fill", borderColor);
    svgText.setAttributeNS(null, "stroke", borderColor);
    svgText.innerHTML = "";
    let textNode = document.createTextNode("FOTO");
    svgText.appendChild(textNode);
  } else if (acessoWebFrameTypes.isRGBack()) {
    if (!react1) {
      react1 = document.createElementNS(xmlns, "rect");
    }
    react1.setAttributeNS(null, "id", `rect1`);
    react1.setAttributeNS(null, "class", `cls-focus`);
    react1.setAttributeNS(null, "x", point4X + (mWidth - mWidth * 0.15));
    react1.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) - mHeight * 0.28
    );
    react1.setAttributeNS(null, "width", mWidth * 0.0735);
    react1.setAttributeNS(null, "height", mHeight * 0.198);
    react1.setAttributeNS(null, "stroke-width", 3);
    react1.setAttributeNS(null, "fill", "none");
    react1.setAttributeNS(null, "stroke", borderColor);

    if (!react2) {
      react2 = document.createElementNS(xmlns, "rect");
    }
    react2.setAttributeNS(null, "id", `rect2`);
    react2.setAttributeNS(null, "class", `cls-focus`);
    react2.setAttributeNS(null, "x", point4X + (mWidth - mWidth * 0.3));
    react2.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) - mHeight * 0.28
    );
    react2.setAttributeNS(null, "width", mWidth * 0.0735);
    react2.setAttributeNS(null, "height", mHeight * 0.49);
    react2.setAttributeNS(null, "stroke-width", 3);
    react2.setAttributeNS(null, "fill", "none");
    react2.setAttributeNS(null, "stroke", borderColor);

    if (!svgText) {
      svgText = document.createElementNS(xmlns, "text");
    }
    svgText.setAttributeNS(null, "id", `text1`);
    svgText.setAttributeNS(null, "class", `cls-text`);
    svgText.setAttributeNS(null, "x", point4X + (mWidth - (mWidth * 0.3 - (mWidth * 0.0735) / 2)));
    svgText.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) - mHeight * 0.45
    );
    svgText.setAttributeNS(null, "stroke", borderColor);
    svgText.setAttributeNS(null, "fill", borderColor);
    svgText.setAttributeNS(null, "style", "writing-mode: tb;");

    svgText.innerHTML = "";
    let textNode = document.createTextNode("NOME");
    svgText.appendChild(textNode);

  } else if (acessoWebFrameTypes.isNewRGFront()) {

    if (!react1) {
      react1 = document.createElementNS(xmlns, "rect");
    }
    react1.setAttributeNS(null, "id", `rect1`);
    react1.setAttributeNS(null, "class", `cls-focus`);
    react1.setAttributeNS(null, "x", point4X + (mWidth - mWidth * 0.58 - mWidth * 0.09));
    react1.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) - mHeight * 0.4
    );
    react1.setAttributeNS(null, "width", mWidth * 0.58);
    react1.setAttributeNS(null, "height", mHeight * 0.3);
    react1.setAttributeNS(null, "stroke-width", 3);
    react1.setAttributeNS(null, "fill", "none");
    react1.setAttributeNS(null, "stroke", borderColor);

    if (!svgText) {
      svgText = document.createElementNS(xmlns, "text");
    }
    svgText.setAttributeNS(null, "id", `text1`);
    svgText.setAttributeNS(null, "class", `cls-text`);
    svgText.setAttributeNS(null, "x", point4X + (mWidth - mWidth * 0.58 - mWidth * 0.09));
    svgText.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) - mHeight * 0.42
    );
    svgText.setAttributeNS(null, "fill", borderColor);
    svgText.setAttributeNS(null, "stroke", borderColor);
    svgText.innerHTML = "";
    let textNode = document.createTextNode("FOTO");
    svgText.appendChild(textNode);
  } else if (acessoWebFrameTypes.isNewRGBack()) {
    if (!react1) {
      react1 = document.createElementNS(xmlns, "rect");
    }
    react1.setAttributeNS(null, "id", `rect1`);
    react1.setAttributeNS(null, "class", `cls-focus`);
    react1.setAttributeNS(null, "x", point4X + mWidth * 0.09);
    react1.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) +
      (mHeight * 0.25) / 2
    );
    react1.setAttributeNS(null, "width", mWidth * 0.62);
    react1.setAttributeNS(null, "height", mHeight * 0.3);
    react1.setAttributeNS(null, "stroke-width", 3);
    react1.setAttributeNS(null, "fill", "none");
    react1.setAttributeNS(null, "stroke", borderColor);

    if (!svgText) {
      svgText = document.createElementNS(xmlns, "text");
    }
    svgText.setAttributeNS(null, "id", `text1`);
    svgText.setAttributeNS(null, "class", `cls-text`);
    svgText.setAttributeNS(null, "x", point4X + mWidth * 0.09);
    svgText.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) +
      (mHeight * 0.25) / 2 -
      10
    );
    svgText.setAttributeNS(null, "fill", borderColor);
    svgText.setAttributeNS(null, "stroke", borderColor);
    svgText.innerHTML = "";
    let textNode = document.createTextNode("DIGITAL");
    svgText.appendChild(textNode);
  } else if (acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CPF) {
    if (!svgText) {
      svgText = document.createElementNS(xmlns, "text");
    }
    svgText.setAttributeNS(null, "id", `text1`);
    svgText.setAttributeNS(null, "class", `cls-text-medium`);
    svgText.setAttributeNS(null, "x", point4X + mWidth * 0.4);
    svgText.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) - mHeight * 0.45
    );
    svgText.setAttributeNS(null, "stroke", borderColor);
    svgText.setAttributeNS(null, "fill", borderColor);
    svgText.setAttributeNS(null, "style", "writing-mode: tb;");
    svgText.innerHTML = "";
    let textNode = document.createTextNode("000.000.000-00");
    svgText.appendChild(textNode);

    if (!svgText2) {
      svgText2 = document.createElementNS(xmlns, "text");
    }
    svgText2.setAttributeNS(null, "id", `text2`);
    svgText2.setAttributeNS(null, "class", `cls-text-big`);
    svgText2.setAttributeNS(null, "x", point4X + (mWidth - (mWidth * 0.3 - (mWidth * 0.09) / 2)));
    svgText2.setAttributeNS(
      null,
      "y",
      (acessoWebFrameSupport.isMobile() ? mBoxYCenter - point6Y : mBoxYCenter) - mHeight * 0.3
    );
    svgText2.setAttributeNS(null, "stroke", borderColor);
    svgText2.setAttributeNS(null, "fill", borderColor);
    svgText2.setAttributeNS(null, "style", "writing-mode: tb;");

    svgText2.innerHTML = "";
    let textNode2 = document.createTextNode("CPF");
    svgText2.appendChild(textNode2);
  }

  if (!exists) {
    groupMask.appendChild(pathBackground);
    groupMask.appendChild(pathFocus);

    if (acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CNH) {
      groupMask.appendChild(pathLine);
    }

    if (
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CNH ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_FRONT ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_BACK ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_FRONT ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_BACK
    ) {
      groupMask.appendChild(react1);
    }

    if (
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CNH ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_FRONT ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_BACK
    ) {
      groupMask.appendChild(react2);
    }

    if (
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CNH ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_FRONT ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_BACK ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_FRONT ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_BACK ||
      acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CPF
    ) {
      groupMask.appendChild(svgText);
    }

    if (acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CPF) {
      groupMask.appendChild(svgText2);
    }

    groupMain.appendChild(groupMask);
    defs.appendChild(style);
    svgMask.appendChild(defs);
    svgMask.appendChild(groupMain);
    boxCamera.appendChild(svgMask);
  }
}

function changeColorMask(color, boxCamera) {
  if (!FocusSilhouette) {
    FocusSilhouette = boxCamera.querySelector("#focus-silhouette");
  }
  if (FocusSilhouette.getAttribute("style") !== `stroke: ${color};`) {
    FocusSilhouette.setAttribute("style", `stroke: ${color};`);
  }
}

export {
    bottomSilhouetteDocument,
    createSelfieMask,
    setBorderColor,
    setBorderWidth,
    createDocumentMask,
    resetVariables,
    showToDisplayMask,
    hideToDisplayMask,
    changeColorMask
}