import * as faceapi from "face-api.js";
import * as timers from "./frame-timers.js"
import * as acessoWebFrameErrors from "./frame-errors.js";

export let isfaceApiLoaded = false;

export async function loadModelsCameraInteligence(urlPathModels) {
  if (!isfaceApiLoaded) {
    timers.add(1, 'Loaded models face api', Date.now(), true);
    return await Promise.all([
      faceapi.nets.tinyFaceDetector.loadFromUri(urlPathModels),
      faceapi.nets.faceLandmark68Net.loadFromUri(urlPathModels)
    ])
      .then(() => {
        timers.set(1, Date.now());
        if(isFaceDetectionModelLoaded && isFaceLandmark68NetLoaded){
          isfaceApiLoaded = true;
          return Promise.resolve();
        }
        else {
          return Promise.reject(
            acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_111)
          );
        }
      })
      .catch(() => {
        timers.set(1, Date.now());
        return Promise.reject(
          acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_111)
        );
      });
  } else {
    return Promise.resolve();
  }
}

export function getFaceDetectorOptions() {
  return new faceapi.TinyFaceDetectorOptions({
    inputSize: 224,
    scoreThreshold: 0.5
  });
}

export function isFaceDetectionModelLoaded() {
  return !!faceapi.nets.tinyFaceDetector.params;
}

export function isFaceLandmark68NetLoaded() {
  return !!faceapi.nets.faceLandmark68Net.params;
}
