import { SignJWT } from 'jose-browser-runtime/jwt/sign';

async function encrypt(info, base64) {

    const value = "aHlwaGVuIGNvbnRlbnRseSBiYWtpbmcgc3RhbGVtYXRl";

    return new SignJWT({
        info: info,
        image: base64
    }).setProtectedHeader({
        alg: 'HS256',
        typ: 'JWT'
    }).setIssuedAt()
        .setIssuer(window.location.href)
        .setExpirationTime('10m')
        .sign(new TextEncoder().encode(atob(value)));
}

export {
    encrypt
}