let stack = [];

function add(error) {
    if(error){
        let errorMounted = {
          column: error.column ? error.column : null,
          line: error.line ? error.line : null,
          message: error.message ? error.message : null,
          constraint: error.constraint ? error.constraint : null,
          name: error.name ? error.name : null,
          stack: error.stack ? error.stack : null
        }
        stack.push(errorMounted);
    }
    else {
        stack.push({ message: "objeto de erro nulo" });
    }
}

function reset() {
    stack = [];
}

function get() {
    return stack;
}

export {
    add,
    reset,
    get
}