/*
1 - loaded models face api
2 - all steps timer
3 - global timer
4 - start camera
6 - time waiting inference models
7 - inactivity
*/

let timerLogs = [];

class TimerLog {
    constructor(id, description, initialTimer){
        this.id = id,
        this.description = description,
        this.initial = initialTimer,
        this.final = null,
        this.timer = null
    }
}

function addManually(log){
    timerLogs.push(log);
}

function add(id, description, initialTimer, resetTimerIfExists) {
    let exists = timerLogs.filter(function(log) { return log.id === id; }).length > 0;
    if (!exists) {
        timerLogs.push(new TimerLog(id, description, initialTimer));
    }
    else if (resetTimerIfExists) {
        reset(id, initialTimer);
    }
};

function set(id, finalTimer) {
    for (let i = 0; i < timerLogs.length; i++) {
        if (timerLogs[i].id === id) {
            timerLogs[i].final = finalTimer;
            timerLogs[i].timer = finalTimer - timerLogs[i].initial;
            break;
        }
    }
};

function reset(id, initialTimer) {
    for (let i = 0; i < timerLogs.length; i++) {
        if (timerLogs[i].id === id) {
            timerLogs[i].initial = initialTimer;
            timerLogs[i].final = null;
            timerLogs[i].timer = null;
            break;
        }
    }
};

function get(id) {
    let log = timerLogs.find(function(log) { return log.id === id; });
    return log === undefined ? null : log;
};

function convertMillisecondsToSeconds(miliseconds) {
    let seconds = miliseconds / 1000;
    return seconds < 1 ? 1 : Math.round(seconds);
}

function compareMilliseconds(milliseconds1, milliseconds2) {
    let interval = milliseconds1 < milliseconds2 ? milliseconds2 - milliseconds1 : milliseconds1 - milliseconds2;
    return convertMillisecondsToSeconds(interval);
}

function getObjectLog(TYPEPROCESS) {
    let tt = get(2);
    let ta = get(4);
    let tw = get(6);
    let tc = get(3);
    let tm = get(1);

    let logs = {
        tt: tt != null ? tt.timer : 0,
        ta: ta != null ? ta.timer : 0,
        tw: tw != null ? tw.timer : 0,
        tc: tc != null ? tc.timer : 0,
        tm: tm != null ? tm.timer : 0,
        tp: TYPEPROCESS
    }

    return logs;
}

function clearAll(){
  timerLogs = [];
}

export {
    addManually,
    add,
    set,
    reset,
    get,
    clearAll,
    convertMillisecondsToSeconds,
    compareMilliseconds,
    getObjectLog
}