
let isCentralized = false;

let centralized = {
  silhoutteWidth: null,
  topSILHOUETTEThresholdVertical: null,
  bottomSILHOUETTEThresholdVertical: null,
  inSILHOUETTEThresholdHorizontal: null,
  overSILHOUETTEThresholdHorizontal: null,
  faceTurnedSILHOUETTEThresholdHorizontal: null,
  CSPWidthLeft: null,
  CSPWidthRight: null,
  CSPHeightTop: null,
  CSPHeightBottom: null,
  distanceLeftByNose: null,
  distanceRightByNose: null,
  differenceInDistance: null,
  differenceLeftY: null,
  differenceRightY: null,
  differenceNoseYThreshold: null
};

let porcentages = {  
  topSilhouetteThresholdVertical: 0,
  bottomSilhouetteThresholdVertical: 0,
  inSilhouetteThresholdHorizontal: 0,
  overSilhouetteThresholdHorizontal: 0,
  faceTurnedSilhouetteThresholdHorizontal: 0,
  differenceNoseYThreshold: 0
};

const messageNotCheck = "Enquadre o rosto no contorno";

function resetCentralized() {
  isCentralized = false;
}

function isCentralizedFace(
    leftX, leftY, rightX, rightY, noseX, noseY,
    silhouetteWidth,
    resolutionWidth, resolutionHeight,
    cameraVideo,
    isMobile
  ) {
        
  setCentralizedParameters(
      leftX, leftY, rightX, rightY, noseX, noseY,
      silhouetteWidth,
      resolutionWidth, resolutionHeight,
      cameraVideo,
      isMobile
    );

  isCentralized = verifyCentralizedIsCheck(leftX, rightX, noseY);
};

function getPercentagesCalc(resolutionWidth, resolutionHeight, cameraVideo, isMobile) {

  if (isMobile) {
    porcentages.topSilhouetteThresholdVertical = 8;
    porcentages.bottomSilhouetteThresholdVertical = 3.47;
    porcentages.inSilhouetteThresholdHorizontal = 10;
    porcentages.overSilhouetteThresholdHorizontal = 4;
    porcentages.faceTurnedSilhouetteThresholdHorizontal = 15;
    porcentages.differenceNoseYThreshold = 7;
  }
  else {
    if(resolutionWidth === 640 && resolutionHeight === 480){
      if (cameraVideo.offsetWidth > 1500) {
        porcentages.topSilhouetteThresholdVertical = 6;
        porcentages.bottomSilhouetteThresholdVertical = 5;
        porcentages.inSilhouetteThresholdHorizontal = 4;
        porcentages.overSilhouetteThresholdHorizontal = 14;
        porcentages.faceTurnedSilhouetteThresholdHorizontal = 9;
        porcentages.differenceNoseYThreshold = 9;
      }
      else if (cameraVideo.offsetWidth < 1499 && cameraVideo.offsetWidth > 850) {
        porcentages.topSilhouetteThresholdVertical = 6;
        porcentages.bottomSilhouetteThresholdVertical = 2;
        porcentages.inSilhouetteThresholdHorizontal = -1.5;
        porcentages.overSilhouetteThresholdHorizontal = 6;
        porcentages.faceTurnedSilhouetteThresholdHorizontal = 9;
        porcentages.differenceNoseYThreshold = 9;
      }
      else {
        porcentages.topSilhouetteThresholdVertical = 5;
        porcentages.bottomSilhouetteThresholdVertical = 4;
        porcentages.inSilhouetteThresholdHorizontal = 2;
        porcentages.overSilhouetteThresholdHorizontal = 6;
        porcentages.faceTurnedSilhouetteThresholdHorizontal = 15;
        porcentages.differenceNoseYThreshold = 10;
      }
    }
    else {
      if (cameraVideo.offsetWidth > 1500) {
        porcentages.topSilhouetteThresholdVertical = 5;
        porcentages.bottomSilhouetteThresholdVertical = 4;
        porcentages.inSilhouetteThresholdHorizontal = 1.5;
        porcentages.overSilhouetteThresholdHorizontal = 4;
        porcentages.faceTurnedSilhouetteThresholdHorizontal = 10;
        porcentages.differenceNoseYThreshold = 8;
      }
      else if (cameraVideo.offsetWidth < 1499 && cameraVideo.offsetWidth > 600) {
        porcentages.topSilhouetteThresholdVertical = 9;
        porcentages.bottomSilhouetteThresholdVertical = 7;
        porcentages.inSilhouetteThresholdHorizontal = 3;
        porcentages.overSilhouetteThresholdHorizontal = 3.5;
        porcentages.faceTurnedSilhouetteThresholdHorizontal = 19;
        porcentages.differenceNoseYThreshold = 15;
      }
      else {
        porcentages.topSilhouetteThresholdVertical = 10;
        porcentages.bottomSilhouetteThresholdVertical = 8;
        porcentages.inSilhouetteThresholdHorizontal = 5;
        porcentages.overSilhouetteThresholdHorizontal = 10;
        porcentages.faceTurnedSilhouetteThresholdHorizontal = 20;
        porcentages.differenceNoseYThreshold = 16;
      }
    }
  }

  centralized.topSilhouetteThresholdVertical = porcentages.topSilhouetteThresholdVertical / 100 * cameraVideo.offsetHeight;
  centralized.bottomSilhouetteThresholdVertical = porcentages.bottomSilhouetteThresholdVertical / 100 * cameraVideo.offsetHeight;
  centralized.inSilhouetteThresholdHorizontal = porcentages.inSilhouetteThresholdHorizontal / 100 * cameraVideo.offsetWidth;
  centralized.overSilhouetteThresholdHorizontal = porcentages.overSilhouetteThresholdHorizontal / 100 * cameraVideo.offsetWidth;
  centralized.faceTurnedSilhouetteThresholdHorizontal = porcentages.faceTurnedSilhouetteThresholdHorizontal / 100 * cameraVideo.offsetWidth;
  centralized.differenceNoseYThreshold = porcentages.differenceNoseYThreshold / 100 * cameraVideo.offsetHeight;
}

function verifyCentralizedIsCheck(leftX, rightX, noseY){
  return leftX >= centralized.CSPWidthLeft - centralized.overSilhouetteThresholdHorizontal &&
         leftX <= centralized.CSPWidthLeft + centralized.inSilhouetteThresholdHorizontal &&
         rightX <= centralized.CSPWidthRight + centralized.overSilhouetteThresholdHorizontal &&
         rightX >= centralized.CSPWidthRight - centralized.inSilhouetteThresholdHorizontal &&
         noseY >= centralized.CSPHeightTop &&
         noseY <= centralized.CSPHeightBottom &&
         centralized.differenceInDistance < centralized.faceTurnedSilhouetteThresholdHorizontal &&
         centralized.differenceLeftY < centralized.differenceNoseYThreshold &&
         centralized.differenceRightY < centralized.differenceNoseYThreshold &&
         centralized.differenceLeftY > -centralized.differenceNoseYThreshold &&
         centralized.differenceRightY > -centralized.differenceNoseYThreshold;
}

function setCentralizedParameters(
    leftX, leftY, rightX, rightY, noseX, noseY,
    silhouetteWidth,
    resolutionWidth, resolutionHeight, 
    cameraVideo, 
    isMobile) {

  centralized.silhoutteWidth = silhouetteWidth;

  getPercentagesCalc(resolutionWidth, resolutionHeight, cameraVideo, isMobile);

  centralized.differenceLeftY = leftY - noseY;
  centralized.differenceRightY = rightY - noseY;

  centralized.CSPWidthLeft = cameraVideo.offsetWidth / 2 - centralized.silhoutteWidth / 2;
  centralized.CSPWidthRight = cameraVideo.offsetWidth / 2 + centralized.silhoutteWidth / 2;
  centralized.CSPHeightTop = cameraVideo.offsetHeight / 2 - centralized.topSilhouetteThresholdVertical;
  centralized.CSPHeightBottom = cameraVideo.offsetHeight / 2 + centralized.bottomSilhouetteThresholdVertical;
  centralized.distanceLeftByNose = noseX - leftX;
  centralized.distanceRightByNose = rightX - noseX;

  if (centralized.distanceLeftByNose >= centralized.distanceRightByNose) {
    centralized.differenceInDistance = centralized.distanceLeftByNose - centralized.distanceRightByNose;
  } else {
    centralized.differenceInDistance = centralized.distanceRightByNose - centralized.distanceLeftByNose;
  }
}

function getMessageCentralizedIsNotCheck(leftX, rightX, noseY) {
  if (rightX - leftX > centralized.silhoutteWidth) {
    return messageNotCheck;
  } else if (
    rightX - leftX <
    centralized.silhoutteWidth - centralized.inSilhouetteThresholdHorizontal
  ) {
    return messageNotCheck;
  } else if (noseY <= centralized.CSPHeightTop) {
    return messageNotCheck;
  } else if (noseY >= centralized.CSPHeightBottom) {
    return messageNotCheck;
  } else if (leftX <= centralized.CSPWidthLeft - centralized.overSilhouetteThresholdHorizontal) {
    return messageNotCheck;
  } else if (
    rightX >=
    centralized.CSPWidthRight + centralized.overSilhouetteThresholdHorizontal
  ) {
    return messageNotCheck;
  } else if (
    centralized.differenceLeftY > centralized.differenceNoseYThreshold &&
    centralized.differenceRightY > centralized.differenceNoseYThreshold
  ) {
    return messageNotCheck;
  } else if (
    centralized.differenceLeftY < -centralized.differenceNoseYThreshold &&
    centralized.differenceRightY < -centralized.differenceNoseYThreshold
  ) {
    return messageNotCheck;
  } else if (centralized.distanceLeftByNose > centralized.distanceRightByNose) {
    return messageNotCheck;
  } else if (centralized.distanceLeftByNose < centralized.distanceRightByNose) {
    return messageNotCheck;
  }
}

export {
  isCentralizedFace,
  isCentralized,
  getMessageCentralizedIsNotCheck,
  resetCentralized
}
