const CODE_ERROR = {
  ERROR_DEFAULT_100: 100,
  ERROR_101: 101,
  ERROR_102: 102,
  ERROR_103: 103,
  ERROR_104: 104,
  ERROR_105: 105,
  ERROR_106: 106,
  ERROR_107: 107,
  ERROR_108: 108,
  ERROR_109: 109,
  ERROR_110: 110,
  ERROR_111: 111,
  ERROR_112: 112,
  ERROR_113: 113,
  ERROR_114: 114
};

const TYPE_ERROR = {
  DEFAULT: "DefaultError",
  PERMISSION: "PermissionError",
  MODEL: "ModelError",
  SUPPORT: "SupportError",
  LIFECICLE: "LifecicleError",
  SESSION: "SessionError",
  IMPLEMENTATION: "ImplementationError",
};

class WebframeError {
  constructor(code, type, message, stack = null) {
    this.code = code;
    this.message = message;
    this.type = type;
    this.stack = stack;
  }
}

let errors = [
  new WebframeError(CODE_ERROR.ERROR_DEFAULT_100, TYPE_ERROR.DEFAULT, "Ops! Algo inesperado aconteceu"),
  new WebframeError(CODE_ERROR.ERROR_101, TYPE_ERROR.PERMISSION, "Usuário negou permissão de acesso a câmera"),
  new WebframeError(CODE_ERROR.ERROR_102, TYPE_ERROR.SESSION, "Timeout, sessão expirada por tempo máximo para captura excedido"),
  new WebframeError(CODE_ERROR.ERROR_103, TYPE_ERROR.SESSION, "Timeout, sessão expirada por inatividade"),
  new WebframeError(CODE_ERROR.ERROR_104, TYPE_ERROR.LIFECICLE, "Captura encerrada por estar com orientação no modo LANDSCAPE"),
  new WebframeError(CODE_ERROR.ERROR_105, TYPE_ERROR.LIFECICLE, "Captura encerrada por inatividade de tela"),
  new WebframeError(CODE_ERROR.ERROR_106, TYPE_ERROR.LIFECICLE, "Captura encerrada pelo usuário desligar o display"),
  new WebframeError(CODE_ERROR.ERROR_107, TYPE_ERROR.IMPLEMENTATION, "O elemento com id #box-camera não foi encontrado"),
  new WebframeError(CODE_ERROR.ERROR_108, TYPE_ERROR.IMPLEMENTATION, "Tipo de câmera inválido"),
  new WebframeError(CODE_ERROR.ERROR_109, TYPE_ERROR.MODEL, "Modelos não foram carregados previamente"),
  new WebframeError(CODE_ERROR.ERROR_110, TYPE_ERROR.IMPLEMENTATION, "Métodos de Callback não implementados corretamente"),
  new WebframeError(CODE_ERROR.ERROR_111, TYPE_ERROR.MODEL, "Não foi possível baixar os modelos, pode ser erro de diretório"),
  new WebframeError(CODE_ERROR.ERROR_112, TYPE_ERROR.SUPPORT, "Browser não suportado"),
  new WebframeError(CODE_ERROR.ERROR_113, TYPE_ERROR.SESSION, "Não foi possível criptografar a sessão. Tente novamente e caso persista, veja os possíveis motivos em nossa Central de Ajuda: <link aqui>"),
  new WebframeError(CODE_ERROR.ERROR_114, TYPE_ERROR.PERMISSION, "Câmera ocupada por outra aplicação"),
];

function validateCodeError(codeError){
  return codeError === CODE_ERROR.ERROR_DEFAULT_100 ||
         codeError === CODE_ERROR.ERROR_101 ||
         codeError === CODE_ERROR.ERROR_102 ||
         codeError === CODE_ERROR.ERROR_103 ||
         codeError === CODE_ERROR.ERROR_104 ||
         codeError === CODE_ERROR.ERROR_105 ||
         codeError === CODE_ERROR.ERROR_106 ||
         codeError === CODE_ERROR.ERROR_107 ||
         codeError === CODE_ERROR.ERROR_108 ||
         codeError === CODE_ERROR.ERROR_109 ||
         codeError === CODE_ERROR.ERROR_110 ||
         codeError === CODE_ERROR.ERROR_111 ||
         codeError === CODE_ERROR.ERROR_112 ||
         codeError === CODE_ERROR.ERROR_113 ||
         codeError === CODE_ERROR.ERROR_114;
}

function getError(codeError, stack = null) {
    if(validateCodeError(codeError)){
        let error = errors.find((error) => error.code === codeError);
        error.stack = stack;
        return error;
    }
    else {
        throw "Code not found in list of codes";
    }
}

export {
  CODE_ERROR,
  getError
}