let Message = "";
let boxLoading = null;
let boxLoadingHtml = null;

function showBoxLoading() {
  if (!boxLoading) return;
  boxLoading.style.display = "block";
}

function hideBoxLoading() {
  if (!boxLoading) return;
  boxLoading.style.display = "none";
}

function showMessage(message) {
  if (Message && Message.innerHTML !== message) {
    Message.innerHTML = message;
    Message.style.visibility = "visible";
    Message.style.opacity = 1;
  }
}

function hideMessage() {
  Message.innerHTML = "";
  Message.style.visibility = "hidden";
  Message.style.opacity = 0;
}

function setStyleMessage(backgroundColor, color) {
  Message.style.backgroundColor = backgroundColor;
  Message.style.color = color;
}

function setTopLabelMessage(cameraVideo, mHeight) {
  Message.style.top = `${cameraVideo.offsetHeight / 2 - mHeight / 2 - 25}px`;
}

function setConfigurationHtml(loadingHtml) {
  boxLoadingHtml = loadingHtml !== undefined || loadingHtml !== null ? loadingHtml : "";
}

function addHtmlToPopup() {
  boxLoading.innerHTML = boxLoadingHtml;
}

function setBoxCamera(boxCamera) {
  boxLoading = boxCamera.querySelector("#box--loading");
  Message = boxCamera.querySelector("#camera--message");
  addHtmlToPopup();
}

export {
  showBoxLoading,
  hideBoxLoading,
  showMessage,
  hideMessage,
  setStyleMessage,
  setTopLabelMessage,
  setConfigurationHtml,
  setBoxCamera,
};
