function setIntervalRecursivePlay(noPlay = false, isPaused = false) {
  let ts = forwardTimes.length > 0 ? forwardTimes[forwardTimes.length - 1] : 50;
  if (acessoWebFrameTypes.isSmartSelfie()) {
    if (noPlay) {
      return setTimeout(null, 0);
    }
    else if (isPaused) {
      return setTimeout(function () { onPlay(); }, 500);
    }
    else if (ts <= 100) {
      return setTimeout(function () { onPlay(); }, 200);
    }
    else if (ts > 100 && ts <= 350) {
      return setTimeout(function () { onPlay(); }, 80);
    }
    else if (ts > 350) {
      return setTimeout(function () { onPlay(); }, 50);
    }
    else {
      return setTimeout(function () { onPlay(); }, 200);
    }
  }
  else {
    if (noPlay) {
      return setTimeout(null, 0);
    }
    else if (isPaused) {
      return setTimeout(function () { onPlay(); }, 500);
    }
    else {
      return setTimeout(function () { onPlay(); }, 500);
    }
  }
};

