function getLabelDocument() {
  if (acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CNH) {
    return "CNH Aberta";
  } else if (acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CPF) {
    return "CPF";
  } else if (
    acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_FRONT ||
    acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_FRONT
  ) {
    return "RG Frente";
  } else if (
    acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_BACK ||
    acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_BACK
  ) {
    return "RG Verso";
  } else if (acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.OTHERS) {
    return LABEL_DOCUMENT_OTHERS;
  }
}

function setOrientation() {

  let orientation =
    (screen.orientation || {}).type || screen.mozOrientation || screen.msOrientation;

  if (orientation) {
    if (orientation == "landscape-primary" || orientation == "landscape-secondary") {
      videoOrientation = acessoWebFrameTypes.Orientation.LANDSCAPE;
    } else {
      videoOrientation = acessoWebFrameTypes.Orientation.PORTRAIT;
    }
  } else {
    if (boxCamera.offsetWidth > boxCamera.offsetHeight) {
      videoOrientation = acessoWebFrameTypes.Orientation.LANDSCAPE;
    } else {
      videoOrientation = acessoWebFrameTypes.Orientation.PORTRAIT;
    }
  }

  if (cameraOpen) {

    if (acessoWebFrameSupport.isMobile()) {
      if (videoOrientation === acessoWebFrameTypes.Orientation.LANDSCAPE) {
        closeCamera();
        onCallback.on.error(acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_104));
      }
    }
  }
}

function getConstraints() {
  return constraints;
}

function visibilityChange() {
  if (cameraOpen && document.hidden) {
    closeCamera();
    onCallback.on.error(acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_106));
  }
}

function focusOutScreen() {
  if (cameraOpen) {
    closeCamera();
    onCallback.on.error(acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_105));
  }
}

function addEventsGlobal() {
  navigator.mediaDevices.enumerateDevices().catch(handleError);
  window.addEventListener("orientationchange", orientationChange);
  navigator.mediaDevices.ondevicechange = orientationChange;
  document.addEventListener("visibilitychange", visibilityChange, false);
  window.addEventListener("blur", focusOutScreen);
  
  acessoWebFrameDetectDevtools.startDetect();
}

function removeEventsGlobal() {
  window.removeEventListener('orientationchange', orientationChange);
  navigator.mediaDevices.ondevicechange = null;
  document.removeEventListener('visibilitychange', visibilityChange, false);
  window.removeEventListener("blur", focusOutScreen);
  
  acessoWebFrameDetectDevtools.stopDetect();
}

function setLayoutProperties(layout) {

  if (layout === null || layout === undefined) return;

  if (layout.silhouette) {
    if (layout.silhouette.primaryColor && isHexaColorValid(layout.silhouette.primaryColor)) {
      COLOR_SILHOUETTE.PRIMARY = layout.silhouette.primaryColor;
    }
    if (layout.silhouette.secondaryColor && isHexaColorValid(layout.silhouette.secondaryColor)) {
      COLOR_SILHOUETTE.SECONDARY = layout.silhouette.secondaryColor;
    }
    if (layout.silhouette.neutralColor && isHexaColorValid(layout.silhouette.neutralColor)) {
      COLOR_SILHOUETTE.NEUTRAL = layout.silhouette.neutralColor;
    }
  }

  if (layout.buttonCapture) {
    if (layout.buttonCapture.backgroundColor && isHexaColorValid(layout.buttonCapture.backgroundColor)) {
      COLOR_BUTTON_CAPTURE.BACKGROUND = layout.buttonCapture.backgroundColor;
    }
    if (layout.buttonCapture.iconColor && isHexaColorValid(layout.buttonCapture.iconColor)) {
      COLOR_BUTTON_CAPTURE.COLOR = layout.buttonCapture.iconColor;
    }
  }

  if (layout.boxMessage) {
    if (layout.boxMessage.backgroundColor && isHexaColorValid(layout.boxMessage.backgroundColor)) {
      COLOR_MESSAGE.BACKGROUND = layout.boxMessage.backgroundColor;
    }
    if (layout.boxMessage.fontColor && isHexaColorValid(layout.boxMessage.fontColor)) {
      COLOR_MESSAGE.COLOR = layout.boxMessage.fontColor;
    }
  }

  if (layout.boxDocument) {
    if (layout.boxDocument.backgroundColor && isHexaColorValid(layout.boxDocument.backgroundColor)) {
      COLOR_BOX_DOCUMENT.BACKGROUND = layout.boxDocument.backgroundColor;
    }
    if (layout.boxDocument.fontColor && isHexaColorValid(layout.boxDocument.fontColor)) {
      COLOR_BOX_DOCUMENT.COLOR = layout.boxDocument.fontColor;
    }
  }
  acessoWebFramePopup.setConfigurationHtml(
    layout.popupLoadingHtml ? layout.popupLoadingHtml : ""
  );
}

function isHexaColorValid(hexadecimal) {
  return /^#([0-9A-F]{3}){1,2}$/i.test(hexadecimal);
}

function verifyBoxCamera() {
  return document.querySelector("#box-camera") !== null;
}

function verifyCallbackProperties(callback) {
  if (callback === null || callback === undefined) {
    return false;
  }
  if (!callback.on) {
    return false;
  }

  if (typeof callback.on.success !== 'function') {
    return false;
  }
  else if (callback.on.success.length !== 1) {
    return false;
  }

  if (typeof callback.on.error !== 'function') {
    return false;
  }
  else if (callback.on.error.length !== 1) {
    return false;
  }

  if (typeof callback.on.support !== 'function') {
    return false;
  }
  else if (callback.on.support.length !== 1) {
    return false;
  }

  return true;
}

function setFaceMode(configurations) {
  if (configurations.optional) {
    let _FACE_MODE = parseInt(configurations.optional.FACE_MODE);
    if (_FACE_MODE === acessoWebFrameTypes.FACE_MODE_TYPE.FRONT || _FACE_MODE === acessoWebFrameTypes.FACE_MODE_TYPE.BACK) {
      acessoWebFrameTypes.setFaceModeType(_FACE_MODE);
    } else {
      acessoWebFrameTypes.setFaceModeType(acessoWebFrameTypes.FACE_MODE_TYPE.FRONT);
    }
  }
  else {
    acessoWebFrameTypes.setFaceModeType(acessoWebFrameTypes.FACE_MODE_TYPE.FRONT);
  }
}

function verifyTypeCamera(configurations) {
  if (configurations === null || configurations === undefined) return false;
  if (configurations.TYPE &&
    (configurations.TYPE === acessoWebFrameTypes.TYPE_CAMERA.CAMERA_NORMAL ||
      configurations.TYPE === acessoWebFrameTypes.TYPE_CAMERA.CAMERA_INTELIGENCE)) {
    return true;
  }
  else {
    return false;
  }
}

function setTypeCamera(configurations) {

  if (configurations.TYPE === acessoWebFrameTypes.TYPE_CAMERA.CAMERA_NORMAL) {
    acessoWebFrameTypes.setSelfieTypeProcess(acessoWebFrameTypes.TYPE_CAMERA.CAMERA_NORMAL);
    acessoWebFrameTypes.setSelfieTypeProcessInitial(acessoWebFrameTypes.TYPE_CAMERA.CAMERA_NORMAL);
    setFaceMode(configurations);
  }
  else if (configurations.TYPE === acessoWebFrameTypes.TYPE_CAMERA.CAMERA_INTELIGENCE) {
    acessoWebFrameTypes.setSelfieTypeProcess(acessoWebFrameTypes.TYPE_CAMERA.CAMERA_INTELIGENCE);
    acessoWebFrameTypes.setSelfieTypeProcessInitial(acessoWebFrameTypes.TYPE_CAMERA.CAMERA_INTELIGENCE);
  }
}

function initCamera(configurations, callback, layout) {
  if (!verifyCallbackProperties(callback)) {
    let error = acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_110);
    window.console.error(error.type, error);
    return;
  }

  onCallback = callback;

  if (!acessoWebFrameSupport.verifyBrowserSupport()) {
    browserNotSupportCallback();
    return;
  }
  if (!verifyBoxCamera()) {
    onCallback.on.error(acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_107));
    return;
  }

  if (!verifyTypeCamera(configurations)) {
    onCallback.on.error(acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_108));
    return;
  }

  if (configurations.TYPE === acessoWebFrameTypes.TYPE_CAMERA.CAMERA_INTELIGENCE) {
    if (!acessoWebFrameModel.isfaceApiLoaded) {
      onCallback.on.error(acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_109));
      return;
    }
  }

  try {
    let logModels = acessoWebFrameTimers.get(1);
    acessoWebFrameTimers.clearAll();

    if (logModels) {
      acessoWebFrameTimers.addManually(logModels);
    }

    addTimersInitial();

    setLayoutProperties(layout);

    acessoWebFrameTypes.resetProcessVariables();

    setTypeCamera(configurations);

    setControls();

    isLoading = true;
    acessoWebFramePopup.showBoxLoading();

    resetVariables();
  }
  catch (error) {
    acessoWebFrameCatalogError.add(error);
    throw error;
  }

  callAllMethodsInit();
}

function initDocument(configurations, callback, layout) {
  if (!verifyCallbackProperties(callback)) {
    let error = acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_110);
    window.console.error(error.type, error);
    return;
  }

  onCallback = callback;

  if (!acessoWebFrameSupport.verifyBrowserSupport()) {
    browserNotSupportCallback();
    return;
  }
  if (!verifyBoxCamera()) {
    onCallback.on.error(acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_107));
    return;
  }

  if (!verifyTypeDocument(configurations)) {
    onCallback.on.error(acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_108));
    return;
  }

  try {
    acessoWebFrameTimers.clearAll();
    addTimersInitial();

    setLayoutProperties(layout);

    acessoWebFrameTypes.resetProcessVariables();

    setControls();

    isLoading = true;
    acessoWebFramePopup.showBoxLoading();

    resetVariables();

    setTypeDocument(configurations);
  }
  catch (error) {
    acessoWebFrameCatalogError.add(error);
    throw error;
  }

  callAllMethodsInit();
}

function verifyTypeDocument(configurations) {
  if (configurations === null || configurations === undefined) return false;

  if (configurations.TYPE && acessoWebFrameTypes.isDocumentType(configurations.TYPE)) {
    return true;
  }
  else {
    return false;
  }
}

function addTimersInitial() {
  acessoWebFrameTimers.add(2, 'all steps timer', Date.now(), true);
  acessoWebFrameTimers.add(4, 'start camera', Date.now(), true);
}

function setTypeDocument(configurations) {

  if (acessoWebFrameTypes.isDocumentType(configurations.TYPE)) {
    acessoWebFrameTypes.setDocumentTypeProcess(configurations.TYPE)

    if (acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.OTHERS) {
      setTypeDocumentOthers(configurations)
    }
  }
}

function setTypeDocumentOthers(configurations) {

  if (configurations.optional) {
    if (
      configurations.optional.LABEL_DOCUMENT_TYPE_OTHERS !== "" &&
      configurations.optional.LABEL_DOCUMENT_TYPE_OTHERS !== undefined &&
      configurations.optional.LABEL_DOCUMENT_TYPE_OTHERS !== null
    ) {
      LABEL_DOCUMENT_OTHERS = configurations.optional.LABEL_DOCUMENT_TYPE_OTHERS;
    } else {
      LABEL_DOCUMENT_OTHERS = "Outros";
    }
  }
  else {
    LABEL_DOCUMENT_OTHERS = "Outros";
  }
}

export {
  closeCamera,
  initCamera,
  initDocument,
  webFrameModel
};
