import * as bp_crypt from "../support/bp_crypt.js";
import * as string_utils from "../support/string-utils.js";

let deviceList;
let videoList;
let videoLabelsList = [];
let arrBadOriginsDetected = [];
let authOriginId;
let authOriginLabel;

let firstOriginId;
let firstOriginLabel;

let salt = "bl@ckp@nth3r";

function getAuthenticateOrigins() {
    return [
        "23242620312c2820",
        "3626276875767d702b"
    ];
}

function getNotAuthenticateOrigins() {
    return [
        "2a2736",
        "332c3731302429",
        "28242b3c262428",
        "362b2435262428",
        "362b2435",
        "263c272037292c2b2e", 
        "3c2a30262428",
        "2429312037262428",
        "23242e20",
        "23242e20262428",
        "283c"
    ];
}

function getStrArrBadOriginsDetected () {
    return arrBadOriginsDetected.join(' | ');
}

function getArrNotAuthOriginsDetected () {
    return arrBadOriginsDetected;
}

function getAuthenticateOriginId() {
    return authOriginId;
}

function getAuthenticateOriginLabel() {
    return authOriginLabel;
}

function getFirstOriginId() {
    return firstOriginId;
}

function getFirstOriginLabel() {
    return firstOriginLabel;
}


function getVideoList() {
    videoList = deviceList.filter((obj) => {
        return obj.kind === 'videoinput';
    })
    return videoList;
}

function getVideoLabelsList() {
    videoList.forEach(function (valor, chave) {
        if(string_utils.isValidString(valor["label"])){
                videoLabelsList.push(valor["label"]);
        }
    });
    return videoLabelsList;
}

function verifyAuthOrigin(){
    videoList.forEach(function (obj, index) {
        let label  = obj["label"];
        var words = label.split(" ");
        words.forEach(function (word, i) {
            if(getAuthenticateOrigins().includes(bp_crypt.bp_encrypt(salt, word.toLowerCase()))){
                let obj = videoList[index];
                if(!authOriginId) {
                    authOriginId = obj["deviceId"];
                    authOriginLabel = obj["label"];
                }
            }
        });
    });
    // It doesn't have any source allowed.
    if(!authOriginId) {
        videoList.forEach(function (obj, index) {
            if(!firstOriginId) {
                firstOriginId = obj["deviceId"];
                firstOriginLabel = obj["label"];
            }

        });
    }
}

function isValidOrigin() {
    getVideoList();
    getVideoLabelsList();
    verifyAuthOrigin();
    let isValid = true;
    videoLabelsList.forEach(function (label, index) {
        var words = label.split(" ");
        words.forEach(function (word, i) {
            if (getNotAuthenticateOrigins().includes(bp_crypt.bp_encrypt(salt,word.toLowerCase()))) {
                if(string_utils.isValidString(label)){
                    arrBadOriginsDetected.push(label);
                }
                isValid = false;
            }
        });
    });
    return isValid;
}

async function verifyOriginStream() {
    try {
        let md = navigator.mediaDevices;
        let results = false;
        if (!md || !md.enumerateDevices) results = false;
        deviceList = await md.enumerateDevices();
        results = deviceList.some(device => 'videoinput' === device.kind);
        if(results) {
            return Promise.resolve(isValidOrigin());
        }
        return Promise.resolve(false);
    } catch (error) {
        return Promise.reject(error);
    }
}

export {
    verifyOriginStream,
    getStrArrBadOriginsDetected,
    getAuthenticateOriginId,
    getAuthenticateOriginLabel,
    getFirstOriginId,
    getFirstOriginLabel,
    getVideoLabelsList,
    getNotAuthenticateOrigins,
    getArrNotAuthOriginsDetected
}
