
let TYPE_PROCESS = null;
let TYPE_PROCESS_INITIAL = null;
const TYPE_CAMERA = {
  CAMERA_NORMAL: 1,
  CAMERA_INTELIGENCE: 2
};

let TYPE_PROCESS_DOCUMENT = null;
const TYPE_DOCUMENT = {
  CNH: 1,
  CPF: 3,
  OTHERS: 5,
  RG_FRONT: 6,
  RG_BACK: 7,
  NEW_RG_FRONT: 8,
  NEW_RG_BACK: 9
};

let FACE_MODE = null;
const FACE_MODE_TYPE = {
  FRONT: 1,
  BACK: 2
};

const Orientation = {
  PORTRAIT: 1,
  LANDSCAPE: 2
};

function isDocumentCapture() {
    return TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.CNH ||
    TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.CPF ||
    TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.OTHERS ||
    TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.RG_FRONT ||
    TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.RG_BACK ||
    TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.NEW_RG_FRONT ||
    TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.NEW_RG_BACK;
}

function isDocumentType(TYPE) {
   return TYPE === TYPE_DOCUMENT.CNH ||
          TYPE === TYPE_DOCUMENT.CPF ||
          TYPE === TYPE_DOCUMENT.RG_FRONT ||
          TYPE === TYPE_DOCUMENT.RG_BACK ||
          TYPE === TYPE_DOCUMENT.NEW_RG_FRONT ||
          TYPE === TYPE_DOCUMENT.NEW_RG_BACK ||
          TYPE === TYPE_DOCUMENT.OTHERS;
}

function isCameraNormalAndFaceModeBack() {
    return TYPE_PROCESS === TYPE_CAMERA.CAMERA_NORMAL && FACE_MODE === FACE_MODE_TYPE.BACK;
}

function isFrontSelfie() {
    return (TYPE_PROCESS === TYPE_CAMERA.CAMERA_NORMAL && FACE_MODE === FACE_MODE_TYPE.FRONT) || TYPE_PROCESS === TYPE_CAMERA.CAMERA_INTELIGENCE;
}

function isSmartSelfie() {
    return TYPE_PROCESS === TYPE_CAMERA.CAMERA_INTELIGENCE;
}

function isNormalSelfie() {
    return TYPE_PROCESS === TYPE_CAMERA.CAMERA_NORMAL;
}

function isRGFront() {
    return TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.RG_FRONT;
}

function isRGBack() {
    return TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.RG_BACK;
}

function isNewRGFront() {
    return TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.NEW_RG_FRONT;
}

function isNewRGBack() {
    return TYPE_PROCESS_DOCUMENT === TYPE_DOCUMENT.NEW_RG_BACK;
}

function resetProcessVariables() {
    TYPE_PROCESS = null;
    TYPE_PROCESS_INITIAL = null;
    TYPE_PROCESS_DOCUMENT = null;
}

function setSelfieTypeProcess(type) {
    TYPE_PROCESS = type;
}

function setSelfieTypeProcessInitial(type) {
    TYPE_PROCESS_INITIAL = type;
}

function setDocumentTypeProcess(type){
    TYPE_PROCESS_DOCUMENT = type;
}

function setFaceModeType(faceMode){
    FACE_MODE = faceMode;
}

export{
    TYPE_PROCESS,
    TYPE_PROCESS_INITIAL,
    TYPE_CAMERA,
    TYPE_DOCUMENT,
    TYPE_PROCESS_DOCUMENT,
    FACE_MODE,
    FACE_MODE_TYPE,
    Orientation,
    isDocumentCapture,
    isDocumentType,
    isCameraNormalAndFaceModeBack,
    isFrontSelfie,
    isSmartSelfie,
    isNormalSelfie,
    isRGFront,
    isRGBack,
    isNewRGFront,
    isNewRGBack,
    resetProcessVariables,
    setSelfieTypeProcess,
    setSelfieTypeProcessInitial,
    setDocumentTypeProcess,
    setFaceModeType
}