import { addListener, launch, stop, removeListener, setDetectDelay } from 'devtools-detector';

let isOpenDevtools = false;

function startDetect() {
  isOpenDevtools = false;
  setDetectDelay(1000);
  addListener(function (isOpen) {
      if (!isOpenDevtools) {
          isOpenDevtools = isOpen;
      }
  });  
  launch();
}

function stopDetect() {
  stop();
  removeListener();
}

function isOpenDevtoolsOpened() {
    return isOpenDevtools;
}

export {
    startDetect,
    stopDetect,
    isOpenDevtoolsOpened
}