class SmartBlink {
    constructor() {
      this.countBlink = 0;
      this.lastBlink;
      this.blinks = [false];        
    }
}

let smartBlink = new SmartBlink();

function add(yLeftEyeBottom, yLeftEyeTop) {
    // yLeftEyeBottom - Quinto ponto do olho, parte de baixo.
    // Terceiro ponto do olho, parte de cima.
    let diffOpenEyes = yLeftEyeTop - yLeftEyeBottom;
    let difference = diff(smartBlink.lastBlink, Math.abs(diffOpenEyes));

    let lastPositionArray = smartBlink.blinks.length - 1;
    let statusBlinkBefore = smartBlink.blinks[lastPositionArray];

    if (difference > 0.70 && !statusBlinkBefore) {
        smartBlink.countBlink++;
        smartBlink.blinks.push(true);
    } else {
        smartBlink.blinks.push(false);
    }

    smartBlink.lastBlink = Math.abs(diffOpenEyes);
};

function reset() {
    smartBlink = new SmartBlink();
}

function getTotalblinks() {
    return smartBlink.countBlink;
}

function diff(num1, num2) {
    return num1 > num2 ? num1 - num2 : num2 - num1;
};

export {
    add,
    getTotalblinks,
    reset
}