import * as acessoWebFrameErrors from "./frame-errors.js";

let _isChrome = (navigator.userAgent.toLowerCase().match(/chrome/i) ? true : false) || (navigator.userAgent.toLowerCase().match(/crios/i) ? true : false) ? true : false;
let _isFirefox = (navigator.userAgent.toLowerCase().match(/firefox/i) ? true : false) || (navigator.userAgent.toLowerCase().match(/fxios/i) ? true : false) ? true : false;
let _isSafari = navigator.userAgent.toLowerCase().match(/safari/i) ? true : false;
let _isEdge = (navigator.userAgent.toLowerCase().match(/edge/i) ? true : false) || (navigator.userAgent.toLowerCase().match(/edgios/i) ? true : false) || (navigator.userAgent.toLowerCase().match(/edg/i) ? true : false) ? true : false;
let _isOpera = (navigator.userAgent.toLowerCase().match(/opera/i) ? true : false) || (navigator.userAgent.toLowerCase().match(/opt/i) ? true : false) || (navigator.userAgent.toLowerCase().match(/opr/i) ? true : false) ? true : false;

function isMobile() {
  return navigator.userAgent.match(/Android/i) ||
    navigator.userAgent.match(/webOS/i) ||
    navigator.userAgent.match(/iPhone/i) ||
    navigator.userAgent.match(/iPad/i) ||
    navigator.userAgent.match(/iPod/i) ||
    navigator.userAgent.match(/BlackBerry/i) ||
    navigator.userAgent.match(/Windows Phone/i)
    ? true
    : false;
}

function isIOS() {
  return navigator.userAgent.match(/webOS/i) ||
    navigator.userAgent.match(/iPhone/i) ||
    navigator.userAgent.match(/iPad/i) ||
    navigator.userAgent.match(/iPod/i) ||
    navigator.userAgent.match(/Mac OS/i)
    ? true
    : false;
}

function isAndroid() {
  return navigator.userAgent.match(/Android/i) ? true : false;
}

function isSafari () {
  if (_isSafari && !_isChrome && !_isEdge && !_isFirefox && !_isOpera) {
    return true;
  } else {
    return false;
  }
}

function verifyBrowserSupport() {

  if (isMobile()) {
    if (isAndroid()) {
      
      if ((_isChrome || _isFirefox) && (!_isOpera && !_isEdge)) {
        return true;
      } else { 
        return false;
      }
    } else if (isIOS()) {
      if (isSafari()) {
        return true;
      } else {
        return false;
      }
    }
  } else {
    if (!isIOS()) {
      if ((_isChrome || _isFirefox) && (!_isOpera && !_isEdge)) {
        return true;
      } else {
        return false;
      }
    } else if (isIOS()) {
      if (isSafari()) {
        return true;
      } else {
        return false;
      }
    }
  }
}

function browserNotSupport() {
  if (isMobile()) {
    if (isAndroid()) {
      let error = acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_112, [{
        message: "Navegadores permitidos:",
        listBrowsersSupport: ["Chrome", "Firefox"],
      }]);

      return error;
    } else if (isIOS()) {

      let error = acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_112, [{
        message: "Navegadores permitidos:",
        listBrowsersSupport: ["Safari"]
      }]);

      return error;
    }
  } else {
    if (isIOS()) {
      let error = acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_112, [{
        message: "Navegadores permitidos:",
        listBrowsersSupport: ["Safari"]
      }]);

      return error;
    } else {
      let error = acessoWebFrameErrors.getError(acessoWebFrameErrors.CODE_ERROR.ERROR_112, [{
        message: "Navegadores permitidos:",
        listBrowsersSupport: ["Chrome", "Firefox"],
      }]);

      return error;
    }
  }
}

function isEmulatedDevice() {
  // maxTouchPoints is always = 1 on simulation mode
  return (
    isTouchDevice() &&
    navigator.maxTouchPoints === 1 &&
    // getHasLiedBrowser solution finding 'mobile' in UA covers `getHasLiedBrowser` missing emulation on android devices
    (getHasLiedBrowser() || window.navigator.userAgent.indexOf('Mobile') !== -1)
  );
}

function getHasLiedBrowser() {
  var userAgent = navigator.userAgent.toLowerCase()
  var productSub = navigator.productSub

  // we extract the browser from the user agent (respect the order of the tests)
  var browser
  if (userAgent.indexOf('edge/') >= 0 || userAgent.indexOf('iemobile/') >= 0) {
    // Unreliable, different versions use EdgeHTML, Webkit, Blink, etc.
    return false
  } else if (userAgent.indexOf('opera mini') >= 0) {
    // Unreliable, different modes use Presto, WebView, Webkit, etc.
    return false
  } else if (userAgent.indexOf('firefox/') >= 0) {
    browser = 'Firefox'
  } else if (userAgent.indexOf('opera/') >= 0 || userAgent.indexOf(' opr/') >= 0) {
    browser = 'Opera'
  } else if (userAgent.indexOf('chrome/') >= 0) {
    browser = 'Chrome'
  } else if (userAgent.indexOf('safari/') >= 0) {
    if (userAgent.indexOf('android 1.') >= 0 || userAgent.indexOf('android 2.') >= 0 || userAgent.indexOf('android 3.') >= 0 || userAgent.indexOf('android 4.') >= 0) {
      browser = 'AOSP'
    } else {
      browser = 'Safari'
    }
  } else if (userAgent.indexOf('trident/') >= 0) {
    browser = 'Internet Explorer'
  } else {
    browser = 'Other'
  }

  if ((browser === 'Chrome' || browser === 'Safari' || browser === 'Opera') && productSub !== '20030107') {
    return true
  }

  // eslint-disable-next-line no-eval
  var tempRes = eval.toString().length
  if (tempRes === 37 && browser !== 'Safari' && browser !== 'Firefox' && browser !== 'Other') {
    return true
  } else if (tempRes === 39 && browser !== 'Internet Explorer' && browser !== 'Other') {
    return true
  } else if (tempRes === 33 && browser !== 'Chrome' && browser !== 'AOSP' && browser !== 'Opera' && browser !== 'Other') {
    return true
  }

  // We create an error to see how it is handled
  var errFirefox
  try {
    // eslint-disable-next-line no-throw-literal
    throw 'a'
  } catch (err) {
    try {
      err.toSource()
      errFirefox = true
    } catch (errOfErr) {
      errFirefox = false
    }
  }
  return errFirefox && browser !== 'Firefox' && browser !== 'Other'
}

function isTouchDevice() {
  var match = window.matchMedia || window.msMatchMedia;
  if (match) {
    var mq = match("(pointer:coarse)");
    return mq.matches;
  }
  return false;
}

const isFirefox = typeof InstallTrigger !== "undefined";

export {
  isMobile,
  isIOS,
  isAndroid,
  isSafari,
  verifyBrowserSupport,
  browserNotSupport,
  isEmulatedDevice,
  isFirefox,
}


